.contenedor-tarea{
    min-width: 65px;
    background-color: #1b1b32;
    margin: 5px 0;
    padding: 8px 15px 8px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 7px;
    border: 1px solid white;
    cursor: pointer;
}

.contenedor-tarea:nth-child(3n + 1){
    background-color: #2B634C;
}

.contenedor-tarea:nth-child(3n + 2){
    background-color: #2F7A70;
}

.contenedor-tarea:nth-child(3n + 3){
    background-color: #2B6770;
}

.texto-tarea{
    width: 100%;
    height: 100%;
    font-size: 20px;
    display: flex;
    align-items: center;
    overflow-wrap: anywhere;
}

.tarea-icono{
    width: 25px;
    height: 25px;
    margin: 5px;
}

.contenedor-tarea.completada{
    background-color: #63AB00;
    text-decoration: line-through;
}

@media screen and (max-width: 500px){
    .texto-tarea{
        font-size: 16px
    }
}